export default {
	props:{
		value:{
			type:String,
			default:null
		},
		//限制上传图片数量
		length:{
			type:Number,
			default:20
		},
		disabled:{
			type:Boolean,
			default:false
		},
		count:{
			type:Number,
			default:9
		}
	},
	data(){
		return {
			videoList:[]
		}
	},
	methods:{
		//上传视频
		upload(){
			let length = this.length - this.videoList.length 
			let count = this.count
			if(length - count < 0){
				count = length
			}
			uni.chooseVideo({
				count: count,
				sourceType: ['camera', 'album'],
				success:res => {
					this.videoList.push(res.tempFilePath)
					this.$emit('input',this.videoList.join(','))
				}
			});
		},
		//删除某项
		delItem(index){
			this.videoList.splice(index,1)
			this.$emit('input',this.videoList.join(','))
		},
		//预览视频
		previewVideo(index){
			
		}
	},
	watch:{
		value:{
			handler(val){
				if(val){
					this.videoList = val.split(',').map((item,index)=>{
						return item
					})
				}
			},
			immediate:true,//使父组件传过来的默认值第一次监听到
		}
	}
}
